/*  notification */
.bookingContent .switch{
	display: block;
	min-width: 50px;
	height: 25px;
	border-radius: 25px;
	background: var(--grey);
	cursor: pointer;
	position: relative;

}
.bookingContent .switch::before {
	content: '';
	position: absolute;
	top: 2px;
	left: 2px;
	bottom: 2px;
	width: calc(25px - 4px);
	background: var(--dark-grey);
	border-radius: 50%;
	transition: all .3s ease;
}
.bookingContent #switch1:checked + .switch::before {
	left: calc(100% - (25px - 4px) - 2px);
	background: blue;
}

.bookingContent #switch2:checked + .switch::before {
	left: calc(100% - (25px - 4px) - 2px);
	background: blue;
}
.bookingdata .h6{
	font-weight: 400;
	font-size: 22px;
}
.changePassword{
